niendo claro el concepto de administración, conozcamos sus características:")]),_c('div',{staticClass:"row justify-content-center mb-5",attrs:{"data-aos":"flip-up"}},[_vm._m(3),_c('div',{staticClass:"col-sm-12 col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{attrs:{"titulo":"Universalidad"}},[_vm._v("La administración está presente en cualquier organismo social, debido a que en todos ellos es necesario coordinar mancomunadamente, los recursos disponibles. Por esta razón, la encontramos en cabeza del Estado, las fuerzas armadas, las empresas, las instituciones educativas, las organizaciones religiosas, entre otros. Es posible encontrar variaciones; según la circunstancia específica, los elementos de la administración se mantienen constantes en todos los casos. Esto la convierte en una disciplina aplicable a cualquier organización social.")]),_c('div',{attrs:{"titulo":"Especificidad"}},[_vm._v("La administración, aunque acompañe a otros fenómenos de naturaleza distinta, posee características que la hacen única y diferente. Por ejemplo, una persona puede ser sobresaliente en su área de recursos humanos, como la psicología organizacional, pero no destacar como administrador. Su carácter particular la distingue de otras ciencias o técnicas.")]),_c('div',{attrs:{"titulo":"Unidad temporal"}},[_vm._v("La administración, a pesar de dividirse en etapas, fases y elementos, es un fenómeno único que está presente continuamente en una organización. Por ejemplo, mientras se elabora un plan, también se toman decisiones, se supervisa y se controla. Los distintos aspectos administrativos coexisten en mayor o menor medida.")]),_c('div',{attrs:{"titulo":"Unidad jerárquica"}},[_vm._v("Todas las personas que ocupan posiciones de mando en una organización participan, de una u otra forma, en el proceso administrativo. En una empresa de industria textil, por ejemplo, este proceso incluye a toda la línea de mando, desde el gerente general hasta los supervisores.")])])],1)]),_c('div